import { useNavigate } from 'react-router';
import InterestPlaceCheckBoxComponent from './InterestPlaceCheckBoxComponent.jsx';

const AskAboutInterestComponent = () => {
    const PLACE_TAG = new Map([
        ["", "accounting"],
        ["", "airport"],
        ["", "amusement_park"],
        ["", "aquarium"],
        ["", "art_gallery"],
        ["ATM", "atm"],
        ["", "bakery"],
        [ "", "bank"],
        ["", "bar"],
        ["", "beauty_salon"],
        ["", "bicycle_store"],
        ["", "book_store"],
        ["", "bowling_alley"],
        ["", "bus_station"],
        ["", "cafe"],
        ["", "campground"],
        ["", "car_dealer"],
        ["", "car_rental"],
        ["", "car_repair"],
        ["", "car_wash"],
        ["", "casino"],
        ["", "cemetery"],
        ["", "church"],
        ["", "city_hall"],
        ["", "clothing_store"],
        ["", "convenience_store"],
        ["", "courthouse"],
        ["", "dentist"],
        ["", "department_store"],
        ["", "doctor"],
        ["", "drugstore"],
        ["", "electrician"],
        ["", "electronics_store"],
        ["", "embassy"],
        ["", "fire_station"],
        ["", "florist"],
        ["", "funeral_home"],
        ["", "furniture_store"],
        ["", "gas_station"],
        ["", "gym"],
        ["", "hair_care"],
        ["", "hardware_store"],
        ["", "hindu_temple"],
        ["", "home_goods_store"],
        ["", "hospital"],
        ["", "insurance_agency"],
        ["", "jewelry_store"],
        ["", "laundry"],
        ["", "lawyer"],
        ["", "library"],
        ["", "light_rail_station"],
        ["", "liquor_store"],
        ["", "local_government_office"],
        ["", "locksmith"],
        ["", "lodging"],
        ["", "meal_delivery"],
        ["", "meal_takeaway"],
        ["", "mosque"],
        ["", "movie_rental"],
        ["", "movie_theater"],
        ["", "moving_company"],
        ["", "museum"],
        ["", "night_club"],
        ["", "painter"],
        ["", "park"],
        ["", "parking"],
        ["", "pet_store"],
        ["", "pharmacy"],
        ["", "physiotherapist"],
        ["", "plumber"],
        ["", "police"],
        ["", "post_office"],
        ["", "primary_school"],
        ["", "real_estate_agency"],
        ["", "restaurant"],
        ["", "roofing_contractor"],
        ["RV", "rv_park"],
        ["", "school"],
        ["", "secondary_school"],
        ["", "shoe_store"],
        ["", "shopping_mall"],
        ["", "spa"],
        ["", "stadium"],
        ["", "storage"],
        ["", "store"],
        ["", "subway_station"],
        ["", "supermarket"],
        ["", "synagogue"],
        ["", "taxi_stand"],
        ["", "tourist_attraction"],
        ["", "train_station"],
        ["", "transit_station"],
        ["", "travel_agency"],
        ["", "university"],
        ["", "veterinary_care"],
        ["", "zoo"]
    ]);
    const navigate = useNavigate();

    let selectedPlaceTag = [];

    const placeTypesJson = localStorage.getItem("selectedPlaceType");
    if (placeTypesJson !== null) {
        const placeTypesDict = JSON.parse(placeTypesJson);
        selectedPlaceTag = Object.keys(placeTypesDict);
    }

    const handleSelectedPlaceTag = (placeTag, checked) => {
        if (checked) {
            if (!selectedPlaceTag.includes(placeTag)) {
                selectedPlaceTag.push(placeTag);
            }
        } else {
            if (selectedPlaceTag.includes(placeTag)) {
                selectedPlaceTag = selectedPlaceTag.filter((value) => (value != placeTag));
            }
        }
    }

    const onClickForStoreLocalStorage = () => {
        const placeTypesDict = localStorage.getItem("selectedPlaceType");
        if (placeTypesDict !== null) {
            localStorage.removeItem("selectedPlaceType");
        }
        let data = {};
        for (let i = 0; i < selectedPlaceTag.length; i++) {
            data[selectedPlaceTag[i]] = PLACE_TAG.get(selectedPlaceTag[i])
        }

        localStorage.setItem('selectedPlaceType', JSON.stringify(data));
        navigate("/");
    }

    return (
        <>
            <div>
                {[...PLACE_TAG.keys()].map((value) => {
                    const selected = selectedPlaceTag.includes(value);
                    return (<InterestPlaceCheckBoxComponent key={value} placeTag={value} checked={selected} setValue={handleSelectedPlaceTag}/>)
                })}

                <label><input type='button' onClick={onClickForStoreLocalStorage}></input></label>
            </div>
        </>
    );
}

export default AskAboutInterestComponent