import { LoadScript } from "@react-google-maps/api";
import React, { useEffect, useState } from "react";

const MapBase = () => {
  const [placeType, setPlaceType] = useState();
  const [selectedButton, setSelectedButton] = useState(null);
  const [selectedPlace, setSelectedPlace] = useState();
  //localStorage
  const placeTypesJson = localStorage.getItem("selectedPlaceType")
  const placeTypesDict = JSON.parse(placeTypesJson);
  const keys = Object.keys(placeTypesDict);
  const handleSelectType = (key) => {
    setPlaceType(placeTypesDict[key]);
    setSelectedButton(key);
  };
  useEffect(() => {
    const successCallback = (position) => {
      const lat = position.coords.latitude;
      const lng = position.coords.longitude;
      const currentLocation = new window.google.maps.LatLng(lat, lng);
      const map = new window.google.maps.Map(document.getElementById("map"), {
        center: currentLocation,
        zoom: 17,
      });
			//
			const currentLocationMarkerIcon = {
      url: "https://maps.google.com/mapfiles/ms/icons/blue-dot.png",
      scaledSize: new window.google.maps.Size(40, 40),
    };
      new window.google.maps.Marker({
        position: currentLocation,
        map: map,
				title: "",
				icon: currentLocationMarkerIcon
      });

      const request = {
        location: currentLocation,
        radius: 200,
        types: [placeType],
      };
			//type
      const service = new window.google.maps.places.PlacesService(map);
      if (placeType) {
        service.nearbySearch(request, (results, status) => {
          if (status === window.google.maps.places.PlacesServiceStatus.OK) {
            for (let i = 0; i < results.length; i++) {
              createMarker(results[i]);
            }
          }
        });
      }
			//
			const markersWithInfowindows = [];
      const createMarker = (place) => {
        const placeLocation = place.geometry.location;
        const marker = new window.google.maps.Marker({
          map: map,
          position: placeLocation,
          title: place.name,
        });

        const infowindow = new window.google.maps.InfoWindow();
				marker.addListener("click", () => {
					markersWithInfowindows.forEach((entry) => {
						entry.infowindow.close()
					});
          const service = new window.google.maps.places.PlacesService(map);
          service.getDetails(
            {
              placeId: place.place_id,
            },
            (result, status) => {
              setSelectedPlace(result)
              if (status === window.google.maps.places.PlacesServiceStatus.OK) {
                infowindow.setContent(`
                  <div style="text-align: center;">
                    <p><b>${result.name}</b></p>
                    <a href=${result.url}>${result.formatted_address}</a>
                    <p>${result.rating ? result.rating + " / 5" : ""}</p>
                    <p>${result.formatted_phone_number ? '<a href=tel:' + result.formatted_phone_number + '>' + result.formatted_phone_number + '</a>' : ""}</p>
                    <ul style="list-style: none; padding: 0; margin: 0;">
                      ${result.current_opening_hours?.weekday_text?.slice(0, 7).map((item, index) => `
                        <li key=${index}>${item}</li>
                      `).join('') || ""}
                    </ul>
                    <p>WEB${result.website ? '<a href=' + result.website + '>' + result.website + '</a>' : ""}</p>
                  </div>
                `);
								infowindow.open(map, marker);
								markersWithInfowindows.push({
                    marker: marker,
                    infowindow: infowindow,
                });
              }
            }
          );
        });
      };
    };

    const errorCallback = (error) => {
      alert("\n");
    };
		//
    navigator.geolocation.getCurrentPosition(successCallback, errorCallback);
  }, [placeType]);
  const addSchedule = () => {
          if (!selectedPlace) {
            alert("");
            return;
          }
          if (window.confirm("")) {
            console.log(selectedPlace.name)
            //MicrosoftGraph
          }
        };

	return (
    <div id="parent-container" style={{
      display: "flex",
      flexDirection: "column",
      height: "650px",
      width: "90%",
      margin: "auto",
      top: "0",
      right: "0",
      bottom: "0",
      left: "0"
    }}>
      <div style={{ display: "flex", flex: 0 }}>
        {keys.map((key) => (
          <button
            style={{
            height: "40px",
            backgroundColor: selectedButton === key ? "#87ceeb" : "transparent",
            }}
            key={key} onClick={() => handleSelectType(key)}>
            {key}
          </button>
        ))}
      </div>
      <div id="map" style={{ flex: 1, width: "100%", border: "1px solid #000" }} />
      <div style={{textAlign: "right"}}>
        <button style={{ height: "40px", marginTop: "10px"}} onClick={() => addSchedule()}></button>
      </div>
		</div>
	);
};

const GoogleMap = () => {
  return (
    <div>
      <LoadScript googleMapsApiKey={process.env.REACT_APP_MAP_API_KEY} libraries={["places"]}>
        <MapBase/>
      </LoadScript>
    </div>
  )
}

export default GoogleMap;
